import { Controller } from '@ali/4ever-cangjie';
import { isKeyHotkey } from 'is-hotkey';
import toggleInlineCode from '../commands/toggleInlineCodeMark';

const isInlineCodeHotKey = isKeyHotkey('mod+e');

export default function onKeyDown(event, controller: Controller, next) {
  if (isInlineCodeHotKey(event)) {
    return toggleInlineCode(controller);
  }
  return next();
}
